body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  font-family: "Open Sans";
  background-color: #f8f8f8;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

input:focus { outline: none; }

input {
    padding-left: 10px;
    padding-right: 10px;
    font-size: 0.9em;
}
.fixed-bottom {
    background-color: #ffffff;
    box-shadow: 0px 4px 14px -2px rgba(107,107,107,0.20);
}

.fixed-bottom loguit {
    color: #2e2e2e;
    font-size: 1.6em;
    margin: 50px auto  
}

.fixed-bottom Link {
    color: #2e2e2e;
    font-size: 1.6em;
    margin: 50px auto  
}



.container {
    background: rgb(247, 247, 247)
}

#loginform input {
    border-radius: 4px;
    box-shadow: 0px 4px 14px -2px rgba(107,107,107,0.30);
    border: none;
    background: #ffffff;
    margin-bottom: 7px;
    height: 40px;
}

#loginform button {
    border: none;
    border-radius: 5px;
    box-shadow: 0px 4px 14px -2px rgba(107,107,107,0.20);
    font-size: 24px;
    color: white;
    background: hsl(0, 100%, 73%);
    padding: 7px 15px;
    margin-top: 30px;
}

#loginform button:hover {
    background: white;
    color: hsl(0, 100%, 73%);
}

#loginform button:focus {
    outline: none;

}
#deletebutton {
    font-size: 1.3em;
    border: none;
    color: hsl(0, 100%, 73%);
    background: white;
    
}

#donebutton {
    font-size: 1.3em;
    border: none;
    color: rgb(113, 197, 64);
}


